<template>
  <div class="pageContent">
    <p class="headerTitle headerTop">:</p>
    <div class="content">
      <div class="contentItem headerTop">
        <span class="contentItemLeftText contentFont">()</span>
        <span class="contentItemRightText contentFont">;</span>
      </div>
      <div class="contentItem">
        <span class="contentItemLeftText contentFont">()</span>
        <span class="contentItemRightText contentFont">;</span>
      </div>
      <div class="contentItem">
        <span class="contentItemLeftText contentFont">()</span>
        <span class="contentItemRightText contentFont">;</span>
      </div>
      <div class="contentItem">
        <span class="contentItemLeftText contentFont">()</span>
        <span class="contentItemRightText contentFont">;</span>
      </div>
      <div class="contentItem">
        <span class="contentItemLeftText contentFont">()</span>
        <span class="contentItemRightText contentFont">;</span>
      </div>
      <div class="contentItem">
        <span class="contentItemLeftText contentFont">()</span>
        <span class="contentItemRightText contentFont">;</span>
      </div>
      <div class="contentItem">
        <span class="contentItemLeftText contentFont">()</span>
        <span class="contentItemRightText contentFont"></span>
      </div>
    </div>
    <p class="headerTitle headerTop">:</p>
    <div class="content contentBottom">
      <div class="contentItem headerTop">
        <span class="contentItemLeftText contentFont">()</span>
        <span class="contentItemRightText contentFont">;</span>
      </div>
      <div class="contentItem">
        <span class="contentItemLeftText contentFont">()</span>
        <span class="contentItemRightText contentFont">;</span>
      </div>
      <div class="contentItem">
        <span class="contentItemLeftText contentFont">()</span>
        <span class="contentItemRightText contentFont">;</span>
      </div>
      <div class="contentItem">
        <span class="contentItemLeftText contentFont">()</span>
        <span class="contentItemRightText contentFont">;</span>
      </div>
      <div class="contentItem">
        <span class="contentItemLeftText contentFont">()</span>
        <span class="contentItemRightText contentFont">;</span>
      </div>
      <div class="contentItem">
        <span class="contentItemLeftText contentFont">()</span>
        <span class="contentItemRightText contentFont">;</span>
      </div>
      <div class="contentItem">
        <span class="contentItemLeftText contentFont">()</span>
        <span class="contentItemRightText contentFont"></span>
      </div>
    </div>
    <div class="bottomDiv">
      <div class="refuseBtn" @click="refuseAction"></div>
      <div class="confirmBtn" @click="confirmAction"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Instructions",
  data() {
    return {};
  },
  mounted() {},
  methods: {
    refuseAction: function() {
      this.$dialog.setDefaultOptions({
        messageAlign: "center",
      });
      this.$dialog
        .confirm({
          message: ""
        })
        .then(() => {
          // on confirm
          this.$router.push({
            path: "/"
          });
        })
        .catch(() => {
          // on cancel
        });
    },
    confirmAction: function() {
      this.$router.push({
        path: "/pay"
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
img {
  width: 100%;
  height: 100%;
  display: block;
}
.pageContent {
  background: #f8f8f8;
  padding: 0.1px;
}

.headerTop {
  margin-top: 10px;
}
.headerTitle {
  height: 50px;
  background: rgba(255, 255, 255, 1);
  font-size: 14px;
  font-family: PingFangSC-Medium;
  font-weight: 500;
  color: rgba(51, 51, 51, 1);
  line-height: 50px;
  padding-left: 20px;
  text-align: left;
}
.contentBottom {
  margin-bottom: 100px;
}
.content {
  margin-top: 1px;
  padding: 0.1px;
  background: #fff;
  padding-bottom: 10px;
}
.contentItem {
  display: flex;
}
.contentItemLeftText {
  width: 42px;
  height: 26px;
  line-height: 26px;
  margin-left: 10px;
}
.contentItemRightText {
  width: 303px;
  line-height: 26px;
  text-align: left;
}
.contentFont {
  font-size: 14px;
  font-family: PingFangSC-Regular;
  font-weight: 400;
  color: rgba(149, 149, 149, 1);
}
.bottomDiv {
  height: 91px;
  width: 100%;
  bottom: 0;
  left: 0;
  position: fixed;
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.08);
  z-index: 99;
  display: flex;
}
.refuseBtn {
  width: 160px;
  height: 44px;
  margin-left: 20px;
  margin-top: 13px;
  margin-right: 15px;
  border-radius: 5px;
  border: 1px solid rgba(240, 240, 240, 1);
  font-size: 14px;
  font-family: PingFangSC-Medium;
  font-weight: 500;
  color: rgba(149, 149, 149, 1);
  line-height: 44px;
}
.confirmBtn {
  width: 160px;
  height: 44px;
  margin: auto;
  margin-top: 13px;
  margin-right: 20px;
  background: rgba(255, 141, 89, 1);
  border-radius: 5px;
  font-size: 14px;
  font-family: PingFangSC-Medium;
  font-weight: 500;
  color: rgba(255, 255, 255, 1);
  line-height: 44px;
}
</style>



